import { css } from 'emotion'
import { useState } from 'react';

const LightBox = ({ onVisible, setOnVisible }) => {
  
  const baseStyle = css`
    visibility: ${onVisible ? 'collapse' : 'visible'};
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 99;
  `

  return (
    <div className={ baseStyle } />
  )
}

export default LightBox